const contractAddr = '0x88d025a59ddb28004ad3ea2ed793c5dcebd67033'

let web3;

const desiredNetwork = "1";

if (typeof window.ethereum === "undefined") {
    alert("Looks like you need a Dapp browser to get started.");
    alert("Consider installing MetaMask!");
  } else {
    // In the case the user has MetaMask installed, you can easily
    // ask them to sign in and reveal their accounts:
    ethereum
      .enable()

      // 记住处理他们拒绝请求的案例：
      .catch(function(reason) {
        if (reason === "User rejected provider access") {
          // 用户拒绝访问程序
          // 用户不想登录

        } else {
          // This shouldn't happen, so you might want to log this...

        }
      })

      // 如果他们批准了登录请求，您将收到他们的帐户：
      .then(function(accounts) {
        console.log(ethereum);
        // 您还应验证用户是否在正确的网络上：
        if (ethereum.networkVersion !== desiredNetwork) {
          alert("此应用程序需要主网络，请在MetaMask用户界面中进行切换 UI.");

          // We plan to provide an API to make this request in the near future.
          // https://github.com/MetaMask/metamask-extension/issues/3663
        }

        // 一旦您有了对用户帐户的引用，
        // 您可以建议交易和签名：
        const account = accounts[0];
        console.log(account);

      });
  }

  if (
    typeof window.ethereum !== 'undefined' ||
    typeof window.web3 !== 'undefined'
  ) {
    // Web3 browser user detected. You can now use the provider.
    const provider = window.ethereum || window.web3.currentProvider
    web3 = new Web3(provider)
  }

  try {
    window.web33 = web3.utils
  } catch (e) {
    alert(' open MetaMask')
  }


  const contract = new web3.eth.Contract(abi, contractAddr)

  export const proposaler  = (_to,_value)=>{
    let value = web3.utils.toHex(web3.utils.toWei(_value, 'mwei'))

    contract.methods.execute(_to, value).send({
        from: window.web3.currentProvider.selectedAddress,
        to:contractAddr,
        gas: 500000
    },(error,res)=>{
        console.log(error,res)
        if(error)return;
        // alert('');
        iview.Message.info('提案成功！');

    }).then((error,res)=>{
        console.log(error,res)

    })
  }

  export const confirm=(callback)=>{
     contract.getPastEvents("ConfirmationNeeded",{fromBlock:0,toBlock: 'latest'},(error,event)=>{
        if(error) return;
        console.log(event)
        let obj;
       obj= event.map((tiem,valu)=>{
        let a =tiem.returnValues;
       let  objitem ={}
       objitem.initiator=a.initiator;
       objitem.operation = a.operation;
       objitem.to = a.to;

       objitem.value =  web3.utils.fromWei(a.value._hex,"mwei")


            return objitem
        })

      callback(obj)

    })
  }
  export const confirmBtn=(id)=>{
    contract.methods.confirm(id).send({
        from: window.web3.currentProvider.selectedAddress,
        to:contractAddr,
        gas: 500000
    },(error,res)=>{
        if(error) return
        console.log("confirmBtn",res)
        iview.Message.info('确认成功！');
    })
  }
